body {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  width: 100vw;
  background-color: rgb(27, 1, 56);
  color: white;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


.home-page{
    margin: auto;
    width: 70vw;
    height: 100vh;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
  }
  
  .myimage{
    width: 13vw;
  }
  
  .mydetails{
    line-height: 10px;
  }
  
  .myname{
    color: rgb(83, 20, 153);
  }
  
  .social-logo{
    margin: 0 10px;
    color: rgb(83, 20, 153);
    font-size: 25px;
  }
  
  .logo ul{
    margin: 0px;
    padding: 0;
  }
  
  .logo ul li{
    list-style: none;
    display: inline;
  }
  .role{
    color: rgb(223, 223, 86);
  }
  .Typewriter{
    display: inline;
  }
  
  .scroll{
    position: absolute;
    padding: 0px;
    bottom: 0%; 
    -webkit-animation: mymove 1s; 
            animation: mymove 1s;
    -webkit-animation-iteration-count: infinite;
            animation-iteration-count: infinite;
  }
  
  @-webkit-keyframes mymove {
    from {bottom: 0px;}
    to {bottom: 8px;}
    from {bottom: 0px;}
  }
  
  @keyframes mymove {
    from {bottom: 0px;}
    to {bottom: 8px;}
    from {bottom: 0px;}
  }

  .scroll ul li{
    list-style: none;
    text-align: center;
  }

  .scroll ul{
    padding: 0px;
  }
  
  @media all and (max-width: 900px) {
    .home-page{
      display: flex;
      flex-direction: column;
      justify-content: center;
      height: 100%;
      width: 100%;
      align-items: center;
      margin: 50% auto;
    }
    .myimage{
      width: 150px;
    }
    .mydetails{
      margin-top: 100px;
    }
  
  }
